.fa-fighter-jet{
	top: -50px;
	position: absolute;
	font-size:120px;
	color:black;
}

#preloader{
	z-index: 10;
    overflow: hidden;
	position: fixed;
	top: 0px;
	width: 100%;
	height: 100%;
	animation: showPage 1s 3s;
	background: linear-gradient(to right, var(--dark-color), var(--blue-aeroday));
	animation-fill-mode: forwards;
}

.jetdiv {
	position: absolute;
	top: 50vh;
	margin-left: -50px;
	left: 50%;
	animation: enterScreen 1s, speeder 0.4s linear infinite, leaveScreen 1s 3s;
}
.jetdiv > span {
	height: 5px;
	width: 35px;
	background: #000;
	position: absolute;
	top: 15px;
	border-radius: 2px 10px 1px 0;
}

.jetdiv > span > span:nth-child(1), .jetdiv > span > span:nth-child(2), .jetdiv > span > span:nth-child(3), .jetdiv > span > span:nth-child(4) {
	width: 30px;
	height: 1px;
	background: #000;
	position: absolute;
	animation: fazer1 0.2s linear infinite;
}
.jetdiv > span > span:nth-child(2) {
	top: 3px;
	animation: fazer2 0.4s linear infinite;
}
.jetdiv > span > span:nth-child(3) {
	top: 1px;
	animation: fazer3 0.4s linear infinite;
	animation-delay: -1s;
}
.jetdiv > span > span:nth-child(4) {
	top: 4px;
	animation: fazer4 1s linear infinite;
	animation-delay: -1s;
}
@keyframes fazer1 {
	0% {
		left: 0;
	}
	100% {
		left: -80px;
		opacity: 0;
	}
}
@keyframes fazer2 {
	0% {
		left: 0;
	}
	100% {
		left: -100px;
		opacity: 0;
	}
}
@keyframes fazer3 {
	0% {
		left: 0;
	}
	100% {
		left: -50px;
		opacity: 0;
	}
}
@keyframes fazer4 {
	0% {
		left: 0;
	}
	100% {
		left: -150px;
		opacity: 0;
	}
}
 @keyframes speeder {
	 0% {
		 transform: translate(2px, 1px) rotate(0deg);
	}
	 10% {
		 transform: translate(-1px, -3px) rotate(-1deg);
	}
	 20% {
		 transform: translate(-2px, 0px) rotate(1deg);
	}
	 30% {
		 transform: translate(1px, 2px) rotate(0deg);
	}
	 40% {
		 transform: translate(1px, -1px) rotate(1deg);
	}
	 50% {
		 transform: translate(-1px, 3px) rotate(-1deg);
	}
	 60% {
		 transform: translate(-1px, 1px) rotate(0deg);
	}
	 70% {
		 transform: translate(3px, 1px) rotate(-1deg);
	}
	 80% {
		 transform: translate(-2px, -1px) rotate(1deg);
	}
	 90% {
		 transform: translate(2px, 1px) rotate(0deg);
	}
	 100% {
		 transform: translate(1px, -2px) rotate(-1deg);
	}
}
