 .longfazers span:nth-child(4) {
	 top: 80%;
	 animation: lf4 0.5s linear infinite;
	 animation-delay: -3s;
}
 @keyframes lf {
	 0% {
		 left: 200%;
	}
	 100% {
		 left: -200%;
		 opacity: 0;
	}
}
 @keyframes lf2 {
	 0% {
		 left: 200%;
	}
	 100% {
		 left: -200%;
		 opacity: 0;
	}
}
 @keyframes lf3 {
	 0% {
		 left: 200%;
	}
	 100% {
		 left: -100%;
		 opacity: 0;
	}
}
 @keyframes lf4 {
	 0% {
		 left: 200%;
	}
	 100% {
		 left: -100%;
		 opacity: 0;
	}
}

@keyframes enterScreen {
	from {left: -10%;}
	to {left: 50%;}
}
  
@keyframes leaveScreen {
	from {left: 50%;}
	to {
		left: 120%;
	}
}
  
@keyframes showPage {
	to {
		left: 200%; 
		margin-left: 150%; 
		transform: skewX(53deg);
	}
}
  
@keyframes hideLoader {
	to{
		height: 0;
		width: 0;
	}
}

